.textContainer {
  width: 250px;
  height: max-content;
  padding: 5px 10px;
  border-radius: 5px;
  /* background-color: #333639; */
  /* background: linear-gradient(180deg, #d9e836, #8a960e); */
  background: linear-gradient(180deg, #502c02, #f7ca6a);
  color: black;
  top: 0;
  z-index: 2;
  transition: 0.4s;
}
.hover:hover {
  background: #502c02;
  color: #f9f9fd;
  transition: 0.4s;
}
.triangleDoc,
.trianglePat {
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 20px solid transparent;
  border-top: 70px solid #333639;
  z-index: -1;
}
.triangleDoc {
  position: absolute;
  left: 265px;
  transform: rotate(-40deg);
}
.trianglePat {
  position: absolute;
  left: -10px;
  transform: rotate(30deg);
}
.current {
  background-color: #333639;
}
.doctorTalkInput,
.patientTalkInput {
  position: absolute;
  background: #333639;
  color: white;
  width: 250px;
}
.doctorTalkInput {
  right: 310px;
  top: 15px;
}
.patientTalkInput {
  left: 370px;
  top: 90px;
}
.typing-demo {
  overflow: hidden;
  font-family: monospace;
  font-size: 22px;
}

@keyframes typing {
  0% {
    width: 0;
  }
  100% {
    width: 50px;
  }
}

@media (max-width: 1500px) {
  .typing-demo {
    font-size: 16px;
  }
  .doctorTalkInput,
  .patientTalkInput {
    width: 200px;
  }
  .triangleDoc {
    left: 210px;
  }
  .patientTalkInput {
    left: 300px;
    top: 70px;
  }

  .doctorTalkInput {
    right: 250px;
    top: 15px;
  }
}
@media (max-width: 750px) {
  .textContainer {
    width: auto;
  }
  .doctorTalkInput,
  .patientTalkInput {
    width: 150px;
  }
  .triangleDoc {
    left: 160px;
  }

  .typing-demo {
    font-size: 14px;
  }
}
@media (max-width: 700px) {
  .doctorTalkInput {
    top: 5px;
  }
}
@media (max-width: 500px) {
  .doctorTalkInput,
  .patientTalkInput {
    width: 100px;
  }
  .triangleDoc {
    left: 100px;
  }
  .patientTalkInput {
    left: 250px;
    top: 50px;
  }
  .doctorTalkInput {
    right: 190px;
    top: -20px;
  }
  .typing-demo {
    font-size: 13px;
  }
}
@media (max-width: 370px) {
  .doctorTalkInput,
  .patientTalkInput {
    width: 80px;
  }
  .triangleDoc {
    left: 90px;
  }
  .patientTalkInput {
    left: 225px;
    top: 35px;
  }
  .doctorTalkInput {
    right: 170px;
    top: -25px;
  }
  .typing-demo {
    font-size: 12px;
  }
}
