.opacityFone1 {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 15;
}
.opacityFone1 button {
  max-width: 100px;
}
.modalContent1 {
  width: max-content;
  background-color: rgba(0, 0, 0, 0.85);
  border: 1px solid wheat;
  border-radius: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  z-index: 5;
  min-height: 30vh;
  max-height: max-content;
  padding: 50px 100px;
}
.showCurrentVariants {
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 20px;
  position: relative;
}
.next {
  position: absolute;
  bottom: 0;
  right: 0;
  cursor: pointer;
}
.finalVariants {
  display: flex;
  align-items: center;
  gap: 50px;
}

.sectionResultLeft {
  overflow-y: scroll;
  padding-right: 50px;
  max-height: 70vh;
}
.sectionResultLeft::-webkit-scrollbar {
  width: 5px;
}

.sectionResultLeft::-webkit-scrollbar-track {
  -webkit-box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;
  background-color: #f9f9fd;
  border-radius: 10px;
}

.sectionResultLeft::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: linear-gradient(180deg, #d9e836, #8a960e);
}
.mainResult {
  display: flex;
  justify-content: space-between;
}
.sectionResultRight {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  position: relative;
}
.finnalProgramComment {
  max-width: 350px;
}
.percent {
  position: relative;
  width: 280px;
  height: 280px;
  margin: 15px auto 0 auto;
}

.percent svg {
  position: relative;
  width: 280px;
  height: 280px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.percent h3 {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  right: 105px;
  top: 70px;
  font-size: 24px;
}

.percent svg circle {
  width: 280px;
  height: 280px;
  fill: none;
  stroke-width: 18;
  stroke: #000;
  transform: translate(10px, 10px);
  stroke-dasharray: 692;
  /* stroke-dashoffset: 520; */
}

.percent svg circle:nth-child(1) {
  stroke-dashoffset: 0;
  stroke: #d9e836;
}
.percent svg circle:nth-child(2) {
  stroke: rgba(0, 0, 0, 0.8);
}

.textAreaWrapper {
  max-width: 350px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}
.textAreaWrapper h3 {
  text-align: center;
  margin: 0;
}
.textAreaWrapper h6 {
  align-self: flex-start;
  color: rgb(226, 6, 6);
  margin: 0;
}
#freeResponse {
  background-color: transparent;
  border-radius: 10px;
  color: white;
  padding: 10px;
}
.sendButtonFreeResponse {
  min-width: 200px;
}
.showFinalProgramComment {
  display: none;
}
@keyframes shake {
  0% {
    transform: translate(2px, 2px);
  }
  25% {
    transform: translate(-2px, -2px);
  }
  50% {
    transform: translate(2px, 2px);
  }
  75% {
    transform: translate(-2px, -2px);
  }
  100% {
    transform: translate(0, 0);
  }
}
@media (max-width: 1200px) {
  .modalContent1 {
    padding: 20px 30px;
  }
}
@media (max-width: 1000px) {
  .showCurrentVariants h2 {
    font-size: 18px;
    margin: 5px;
  }
  .showCurrentVariants span {
    margin: 0;
    font-size: 14px;
  }
}
@media (max-width: 770px) {
  .finalVariants {
    justify-content: center;
    gap: 20px;
    max-height: 80vh;
    overflow-y: scroll;
  }
  .next {
    top: 0;
    left: 0;
    height: fit-content;
  }
  .textAreaWrapper {
    margin-top: 50px;
  }
  .mainResult h2 {
    margin: 5px;
    font-size: 16px;
  }
  .mainResult span {
    font-size: 14px;
  }
  .finnalProgramComment {
    font-size: 13px;
    max-width: 300px;
    margin: 0;
    margin-top: -20px;
    margin-bottom: 10px;
    max-height: 100px;
    overflow-y: scroll;
    padding-right: 10px;
  }
  .finnalProgramComment::-webkit-scrollbar {
    width: 5px;
  }

  .finnalProgramComment::-webkit-scrollbar-track {
    -webkit-box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;
    background-color: #f9f9fd;
    border-radius: 10px;
  }

  .finnalProgramComment::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background: linear-gradient(180deg, #d9e836, #8a960e);
  }
}
@media (max-width: 650px) {
  .finalVariants {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

  .textAreaWrapper h3 {
    font-size: 14px;
  }
}
@media (max-width: 650px) {
  .modalContent1 {
    padding: 15px 20px;
    max-width: 96vw;
  }
  .finalVariants {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
  .yourCurrentVariants {
    margin-top: 50px !important;
  }
}
@media (max-width: 550px) {
  .showFinalProgramComment {
    display: flex;
  }
  .finnalProgramComment {
    position: absolute;
    border-radius: 10px;
    overflow-y: hidden;
    height: 0;
    max-height: none;
  }
  .sectionResultLeft {
    padding-right: 20px;
  }
  .mainResult {
    flex-direction: column-reverse;
  }
  .sectionResultLeft {
    max-height: 37vh;
  }
}
@media (max-width: 450px) {
  .modalContent1 {
    padding: 20px 5px;

  }
  .finalVariants {
    gap: 0;
  }
  .showCurrentVariants h2 {
    font-size: 14px;
    margin: 0px;
  }
  .showCurrentVariants span {
    font-size: 12px;
    max-width: 100px;
  }
}
