.opacityFone {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 15;
}
.opacityFone button {
  max-width: 100px;
}
.sendButton {
  border: none;
  padding: 5px 15px;
  border-radius: 10px;
  cursor: pointer;
  background: linear-gradient(180deg, #d9e836, #8a960e);
  transition: 0.8s;
  display: flex;
  justify-content: center;
  align-self: center;
  color: black;
}
.sendButtonAbsolutle {
  position: absolute;
  bottom: 30px;
  left: 30px;
}
.sendButton:hover {
  background: linear-gradient(180deg, #535814, #444907);
  color: white;
  transition: 0.8s;
}

.modalContent {
  width: 60%;
  padding: 10px 30px;
  background-color: rgba(0, 0, 0, 0.85);
  border: 1px solid wheat;
  border-radius: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  z-index: 6;
}
.startButton {
  background-color: antiquewhite;
  border: none;
  width: 80px;
  height: 25px;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 70px;
}
.diagnosisContent {
  height: 80vh;
  display: flex;
  gap: 50px;
  align-items: baseline;
  justify-content: space-around;
  margin-bottom: auto;
}
.charter {
  cursor: pointer;
  width: max-content;
  padding: 0 15px;
  height: 40px;
  border-radius: 10px;
  transition: 0.5;
  opacity: 0;
  animation: showElement 0.6s linear forwards;
}
@keyframes showElement {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    transform: translate(0, 0);
  }
}
.showElement {
  opacity: 1;
}
.variants {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  gap: 10px;
}

.variants span {
  position: relative;
  border: 1px solid white;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 2px 5px;
  border-radius: 10px;
  cursor: pointer;
  font-size: 14px;
  min-height: 50px;
}
@keyframes opacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.treatmentContent {
  padding: 0 50px;
  min-height: 80vh;
  height: max-content;
}
.headerTreatment {
  display: flex;
  justify-content: space-between;
  margin-top: 50px;
}
.headerTreatment div {
  font-size: 24px;
  cursor: pointer;
}
.treatmentMainContent {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
@keyframes welcomeMedicalGroup {
  0% {
  }
  100% {
    opacity: 1;
    transform: translate(0 0);
  }
}
@keyframes welcomeMedCurrent {
  0% {
  }
  100% {
    opacity: 1;
  }
}
.pulsCharter {
  animation: pulsCharter 0.6s linear infinite;
}
.showModalHint {
  position: absolute;
  z-index: 20;
  padding: 20px;
  background: black;
  border-radius: 10px;
  border: 1px solid white;
  opacity: 0;
  animation: opacity 0.5s linear forwards;
}
.showModalHint div:nth-child(1) {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
}
.showModalHint div:nth-child(4) {
  max-height: 70vh;
  overflow-y: scroll;
}
.showModalHint div:nth-child(4)::-webkit-scrollbar {
  width: 0;
}
@keyframes opacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes pulsCharter {
  0% {
    transform: scale(1, 1);
  }
  50% {
    transform: scale(1, 1);
  }
  60% {
    transform: scale(1.1, 1.1);
  }
  70% {
    transform: scale(1, 1);
  }
  80% {
    transform: scale(1.1, 1.1);
  }
  90% {
    transform: scale(1, 1);
  }
  100% {
    transform: scale(1, 1);
  }
}

.treatmentFlex {
  display: flex;
  margin: 40px 0;
  justify-content: center;
}
.currentMed {
  background-color: black;
  border-radius: 0 30px 30px;
  border: 1px solid white;
  width: 60%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 100px 100px 100px 100px 100px;
  gap: 10px;
  padding: 10px;
  margin-left: -1px;
  overflow-y: scroll;
}
.currentMed::-webkit-scrollbar {
  width: 0;
  height: 7px;
}
.currentMed::-webkit-scrollbar-track {
  -webkit-box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;
  background-color: #f9f9fd;
  border-radius: 10px;
}
.currentMed::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: linear-gradient(180deg, #d9e836, #8a960e);
}
.currentMed span {
  border: 1px solid white;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  cursor: pointer;
  padding: 0px 10px;
}
.nonMedicalTreatmentPath {
  text-align: center;
  border: 1px solid white;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  cursor: pointer;
  position: relative;
}
.treatmentMainContent {
  grid-template-columns: 1fr 1fr 1fr 1fr;
}
.forStructure{
  display: none;
}
@media (max-width: 1500px) {
  .modalContent {
    width: 80%;
  }
}
@media (max-width: 1200px) {
  .headerTreatment div {
    font-size: 18px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .currentMed {
    overflow-y: visible;
    grid-template-columns: 1fr;
    gap: 5px;
    grid-template-rows: auto;
  }
  .treatmentFlex {
    margin: 20px 0;
  }
}
@media (max-width: 1000px) {
  .modalContent {
    width: 85%;
  }
  .treatmentMainContent span {
    font-size: 14px;
  }
  .currentMed span {
    font-size: 14px;
  }
  .charter {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .charter h2 {
    font-size: 18px;
  }
  .diagnosisContent {
    gap: 20px;
  }
}
@media (max-width: 790px) {
  .diagnosisContent {
    flex-direction: column;
    justify-content: center;
  }
  .diagnosisContent h4 {
    margin: 10px;
  }
  .charter h2 {
    margin: 0;
  }
  .charterWrapper {
    display: flex;
  }
}
@media (max-width: 770px) {
  .modalContent {
    padding: 5px 20px;
    width: 90vw;
  }
}

@media (max-width: 700px) {
  .treatmentMainContent {
    grid-template-columns: 1fr 1fr 1fr;
  }
  .modalContent {
    padding: 10px;
    width: 99vw;
  }
  .treatmentFlex {
    justify-content: space-between;
  }
  .modalContent h2 {
    font-size: 18px;
  }
  .modalContent li {
    font-size: 14px;
  }
  .charterWrapper {
    display: flex;
    flex-direction: column;
  }
  .diagnosisContent {
    min-height: 90vh;
    height: max-content;
  }
  .variants {
    grid-template-columns: 1fr 1fr;
  }
  .forTreatment {
    position: absolute;
    top: 15px;
    left: 12px;
  }
  .treatmentContent {
    min-height: 95vh;
  }
  .headerTreatment {
    justify-content: flex-start;
  }
  .headerTreatment div {
    font-size: 14px;
  }
  .treatmentContent {
    padding: 0 3px;
  }
}
@media (max-width: 450px) {
  .diagnosisContent {
    gap: 0;
  }
  .treatmentMainContent {
    grid-template-columns: 1fr 1fr;
    gap: 10px;
  }
  .headerTreatment {
    margin-top: 40px;
  }
  .variants span {
    padding: 1px 2px;
    font-size: 14px;
  }
  .variants {
    gap: 3px;
  }
  .treatmentMainContent span {
    font-size: 11px;
  }
  .currentMed span {
    font-size: 11px;
  }
  .treatmentFlex {
    margin: 10px 0;
  }
}
@media (max-width: 370px) {
 
  .variants span {
    font-size: 13px;
    max-width: 160px;
  }
  .variants {
    gap: 2px;
  }
  .currentMed span{
    max-width: 140px;
  }
}
@media (max-width: 320px) {
  .modalContent {
    padding: 15px 2px;
    width: 98vw;
  }
  .variants {
    grid-template-columns: 1fr;
    max-height: 230px;
    overflow-y: scroll;
    margin-bottom: 40px;
  }
  .variants::-webkit-scrollbar {
    width: 0;
  }
  .variants span {
    font-size: 13px;
    max-width: none;
  }
  .treatmentFlex{
    flex-direction: column;
    align-items: center;
  }
  .treatmentMainContent{
    position: absolute;
    height: 0;
    overflow: hidden;
    background-color: black;
    max-width: 100%;
    
    text-align: center;
  }
  .openMedCategory{
    padding: 10px 0;
    height: max-content;
  }
  .forStructure{
    height: 30px;
    background: #8a960e;
    padding: 2px 5px;
    border-radius: 10px;
    display: flex;
    align-items: center;
  }
  .currentMed{
    border: none;
    width: 90%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: transparent;
  }
  .currentMed span{
  width: 100%;
  height: 30px;
  font-size: 12px;
  max-width: none;
    
  }
  .charter span {
    font-size: 12px;
  }
}
