.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  gap: 80px;
  position: relative;
  overflow: hidden;
  background-image: url("./components/Image/doctor_room.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  color: white;
}
.menuButtons {
  position: absolute;
  background: linear-gradient(10deg, #502c0286, #f7ca6a94);
  border-radius: 10px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  top: 50px;
  left: 50px;
  gap: 10px;
  z-index: 2;
}
.hint {
  position: absolute;
  top: -25px;
  right: -25px;
  font-size: 18px;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background: linear-gradient(10deg, #502c02, #f7ca6a);
  cursor: pointer;
  color: black;
}
.hintInfo {
  position: absolute;
  top: 50px;
  background-color: #252525;
  padding: 5px 10px;
  border-radius: 5px;
  color: white;
  z-index: 15;
}
.menuButtons button {
  cursor: pointer;
  border: none;
  font-size: 24px;
  padding: 4px 10px;
  /* background: linear-gradient(10deg, #252525, white); */
  background: linear-gradient(180deg, #502c02, #f7ca6a);
  color: black;
  border-radius: 5px;
  transition: 0.4s;
}
.menuButtons button:hover {
  background: #502c02;
  color: #f9f9fd;
  transition: 0.4s;
}
.menuButtons button:disabled {
  color: #252525;
  background: transparent;
}
.dialogInputs {
  position: absolute;
  top: 0;
}

.doctorPerson {
  width: max-content;
  position: absolute;
  bottom: -30px;
  margin-left: 600px;
}
.doctorPerson img {
  width: 450px;
}
.patientPerson {
  width: max-content;
  position: absolute;
  bottom: -45px;
  margin-right: 700px;
}
.patientPerson img {
  width: 450px;
}
.dialogs {
  position: absolute;
  background-color: #252525c2;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  width: 350px;
  height: 90vh;
  transition: 1s;
  z-index: 5;
}
.dialogsContent {
  overflow-y: scroll;
  padding-right: 15px;
  padding-left: 15px;
  padding-top: 15px;
}
.dialogsContent::-webkit-scrollbar {
  width: 5px;
}

.dialogsContent::-webkit-scrollbar-track {
  -webkit-box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;
  background-color: #f9f9fd;
  border-radius: 10px;
}

.dialogsContent::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: linear-gradient(180deg, #d9e836, #8a960e);
}
.openDialogsButton {
  position: absolute;
  color: white;
  border: none;
  background-color: transparent;
  font-size: 24px;
  cursor: pointer;
}
.poweredByMeroPlace {
  text-align: center;
  background-color: #252525;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  border-radius: 10px;
  margin-top: 20px;
}
.poweredByMeroPlace img {
  width: 90px;
}

.mouseDoctor,
.mousePatient {
  animation: talk 0.3s linear infinite;
  position: absolute;
  background-color: red;
  z-index: 4;
  border-radius: 50%;
}
.mouseDoctor {
  width: 34px;
  left: 192px;
  top: 175px;
}
.mousePatient {
  width: 30px;
  left: 305px;
  top: 185px;
  transform: rotate(-5deg);
}
.openMainMenu {
  display: none;
}
@keyframes talk {
  0% {
    height: 0;
  }
  100% {
    height: 10px;
  }
}
.pulsHint {
  animation: pulsHint 1s linear infinite;
}
@keyframes pulsHint {
  0% {
    transform: scale(1, 1);
    box-shadow: 0 0 0px red;
  }
  25% {
    transform: scale(1.1, 1.1);
    box-shadow: 0 0 7px red;
  }
  50% {
    transform: scale(1, 1);
    box-shadow: 0 0 0px red;
  }
  75% {
    transform: scale(1.1, 1.1);
    box-shadow: 0 0 7px red;
  }
  100% {
    transform: scale(1, 1);
    box-shadow: 0 0 0px red;
  }
}
@media (max-width: 1700px) {
  .menuButtons {
    opacity: 0.8;
  }
  .patientPerson {
    margin-right: 350px;
  }
  .doctorPerson {
    margin-left: 700px;
  }
}
@media (max-width: 1560px) {
  .menuButtons {
    left: 0;
  }
}
@media (max-width: 1500px) {
  .patientPerson {
    margin-right: 450px;
  }
  .doctorPerson {
    margin-left: 400px;
  }
  .doctorPerson img {
    width: 350px;
  }
  .patientPerson img {
    width: 350px;
  }
  .mouseDoctor {
    width: 25px;
    left: 150px;
    top: 135px;
  }
  .mousePatient {
    width: 25px;
    left: 247px;
    top: 143px;
    transform: rotate(-5deg);
  }
  .menuButtons button {
    font-size: 18px;
  }
}
@media (max-width: 1150px) {
  .patientPerson {
    margin-right: 500px;
  }
  .doctorPerson {
    margin-left: 500px;
  }
  .menuButtons {
    gap: 5px;
    top: 30px;
  }
  .poweredByMeroPlace {
    margin-top: 5px;
  }
}

@media (max-width: 800px) {
  .menuButtons {
    gap: 0;
  }
  .poweredByMeroPlace {
    font-size: 12px;
  }
}
@media (max-width: 750px) {
  .dialogs {
    background-color: #252525e8;
    z-index: 15;
    width: 98vw;
  }
  .doctorPerson {
    bottom: 180px;
  }

  .patientPerson {
    bottom: 170px;
  }
  .hint {
    right: -50px;
  }
  .menuButtons {
    z-index: 10;
    background: linear-gradient(10deg, #502c02f8, #f7ca6a);
    transition: 0.7s;
    opacity: 1;
  }
  .openMainMenu {
    display: block;
    position: absolute;
    top: 35px;
    color: black;
    background: linear-gradient(10deg, #502c02, #f7ca6a);
    padding: 5px 10px;
    font-size: 18px;
    font-weight: 700;
    border-radius: 10px;
    cursor: pointer;
  }
}
@media (max-width: 700px) {
  .patientPerson {
    margin-right: 350px;
  }
  .doctorPerson {
    margin-left: 400px;
  }
}
@media (max-width: 630px) {
  .patientPerson {
    margin-right: 380px;
  }
  .doctorPerson {
    margin-left: 300px;
  }
}
@media (max-width: 500px) {
  .patientPerson {
    margin-right: 280px;
  }
  .doctorPerson {
    margin-left: 200px;
  }
  .doctorPerson img {
    width: 300px;
  }
  .patientPerson img {
    width: 300px;
  }
  .mouseDoctor {
    width: 20px;
    left: 130px;
    top: 115px;
  }
  .mousePatient {
    width: 20px;
    left: 220px;
    top: 122px;
  }
}
@media (max-width: 370px) {
  .patientPerson {
    margin-right: 240px;
  }
  .doctorPerson {
    margin-left: 180px;
  }
  .doctorPerson img {
    width: 250px;
  }
  .patientPerson img {
    width: 250px;
  }
  .mouseDoctor {
    width: 15px;
    left: 108px;
    top: 95px;
  }
  .mousePatient {
    width: 15px;
    left: 191px;
    top: 102px;
  }
  .menuButtons button {
    font-size: 15.5px;
  }
}
